/* 메인레이아웃 CSS */

@charset "utf-8";

.wrap {
  position: relative;
}

.header_wrap {
position: relative;
  width: 1200px;
  height: 70px;
  margin: 0 auto;
  text-align: center;
  background-color: brown;
}

.content_wrap {
  width: 1200px;
  margin: 0 auto;
  text-align: center;
  background-color: blue;
}

.footer_wrap {
  position: relative;
  display: block;
  width: 1200px;
  height: 77px;
  text-align: center;
  margin: 0 auto;
  background-color: palegreen;
}